<script lang="ts">
import { defineComponent, onMounted, computed, ref } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default defineComponent({
  name: 'HomeContentView',
  setup() {
    const router = useRouter();
    const store = useStore();

    const isAuthenticated = computed(() => store.getters.isAuthenticated);

    const features = ref([
      {
        icon: 'fas fa-folder-open',
        title: 'Gestion de documents',
        description: 'Organisez vos fichiers par services et retrouvez-les facilement',
        gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
      },
      {
        icon: 'fas fa-share-alt',
        title: 'Partage sécurisé',
        description: 'Partagez vos documents avec vos collègues en un clic',
        gradient: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)'
      },
      {
        icon: 'fas fa-star',
        title: 'Favoris',
        description: 'Accédez rapidement à vos documents les plus importants',
        gradient: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)'
      },
      {
        icon: 'fas fa-clock',
        title: 'Documents récents',
        description: 'Retrouvez facilement vos derniers fichiers consultés',
        gradient: 'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)'
      },
      {
        icon: 'fas fa-lock',
        title: 'Sécurité',
        description: 'Vos données sont cryptées et protégées en permanence',
        gradient: 'linear-gradient(135deg, #fa709a 0%, #fee140 100%)'
      },
      {
        icon: 'fas fa-users',
        title: 'Collaboration',
        description: 'Travaillez en équipe de manière efficace et fluide',
        gradient: 'linear-gradient(135deg, #30cfd0 0%, #330867 100%)'
      }
    ]);

    const steps = ref([
      {
        icon: 'fas fa-user-plus',
        title: 'Inscrivez-vous',
        description: 'Créez votre compte gratuitement en quelques secondes'
      },
      {
        icon: 'fas fa-cloud-upload-alt',
        title: 'Téléchargez',
        description: 'Importez vos documents en toute sécurité'
      },
      {
        icon: 'fas fa-share-nodes',
        title: 'Partagez',
        description: 'Collaborez avec votre équipe instantanément'
      }
    ]);

    const redirectToDashboard = () => {
      console.log('Bouton "Démarrer maintenant" cliqué');
      if (store.getters.isAuthenticated) {
        console.log('Utilisateur authentifié, redirection vers /dashboard');
        router.push('/dashboard');
      } else {
        console.log('Utilisateur non authentifié, redirection vers /connexion');
        router.push('/connexion');
      }
    };

    const goToSignup = () => {
      router.push('/inscription');
    };

    onMounted(() => {
      const observerCallback = (entries: IntersectionObserverEntry[], observer: IntersectionObserver) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            entry.target.classList.add('animate-on-scroll-visible');
            observer.unobserve(entry.target);
          }
        });
      };

      const observer = new IntersectionObserver(observerCallback, {
        root: null,
        threshold: 0.1
      });

      const animatedElements = document.querySelectorAll('.animate-on-scroll');
      animatedElements.forEach(el => {
        observer.observe(el);
      });
    });

    return {
      isAuthenticated,
      features,
      steps,
      redirectToDashboard,
      goToSignup
    };
  }
});
</script>

